export default {
    props: {
        canSend: {
            type: Boolean,
            default: false
        },
        refreshButton:{
            type:Boolean,
            default:false

        }
    },
    data() {
        return {
            timer: 60,
            timing: false,
            bar: ''
        }
    },
    watch:{
        refreshButton(){
            this.refresh();
        }
    },
    methods: {
        clickBuntton() {
            if (!this.timing && this.canSend) {
                this.$emit('click', true);
                this.timing = true;
                this.bar = setInterval(() => {
                    if (this.timer > 1) {
                        this.timer--;
                    } else {
                        this.refresh();
                    }
                }, 1000);
            } else {
                this.$emit('click', false);
            }
        },
        refresh() {
            this.timer = 60;
            this.timing = false;
            clearInterval(this.bar);
        }
    },
    beforeMount() {
        this.refresh();
    },
}
